@charset "utf-8";

@import url('https://fonts.googleapis.com/css?family=Space+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

// Our variables
$base-font-family: 'Bebas Neue', sans-serif;
$alternative-font-family: 'Space Mono', monospace;
$page-font-family: 'Work Sans', sans-serif;
$base-font-size:   30px;
$small-font-size:  $base-font-size * 2;
$base-line-height: 1.5;

$spacing-unit:     1rem;

$text-color:       black;
$header-text-color: #111;
$background-color: #ffffff;

$white-color:      #fafafa;
$strong-orange-color:  #EA817D;
$orange-hover-color:  #EA817D;
$red-hover-color:  #fafafa;
$black-color:       black;

// Width of the content area
$content-width:    1400px;

$on-palm:          600px;
$on-laptop:        800px;
$on-small:        1000px;

$box-width:        100px;
$box-indent:        30px;

$small-width:       25%;
$full-width:       100%;

@mixin media-query($device) {
  @media screen and (max-width: $device) {
    @content;
  }
}

.site-footer .wrapper {
  display: flex;
  flex-direction: column;
}

.footer-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px; /* Adjust spacing between rows if needed */
}

.contact h2, .license p {
  margin: 0;
}

.copyright {
  text-align: center;
  font-size: 0.9em; /* Adjust font size if needed */
  margin-top: 10px;
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
  "base",
  "layout",
  "homepage"
  ;
